import errorImage from '../images/broken-image.png'

export default class NewsService {

    constructor() {
        this._apiBase = `https://newsapi.org/v2/top-headlines?`
        this.country = 'tr'
        
    }

    getResource = async (url) => {

        const res = await fetch(`${this._apiBase}${url}`);

        if( !res.ok ) {
            throw new Error(`Could not fetch ${url}, recieved ${res.status}`)
        }

        return res.json()
    }

    getAllNews = async (country, category, search) => {
        const result = await this.getResource(`q=${search}&country=${country}&category=${category}&apiKey=5303526686bf474bae53768ff7e5990f`)
        return result.articles.map(this._transformPosts)
    }
    
    _transformPosts = (articles) => {
        return {
            title: articles.title, 
            desc: articles.description,
            link: articles.url,
            image: (articles.urlToImage) ? articles.urlToImage : `${errorImage}`,
            imageSource: articles.source.name

        }

    }
}