import React, {Component} from 'react';
import './App.css';
import newsService from './services/newsService'
import Theme from './components/theme'
import Loading from './components/loading'

export default class App extends Component{
	
	newsService = new newsService()
	
	state = {
		articles: [],
		error: false,
		loading: true,
		language: 'tr',
		search: '',
		category: '',
		theme: 'day',
	}
	constructor(props) {
		super(props)
		this.getTheme = React.createRef();
	}
	changeLang = async (event) => {
		
		await this.setState({language: event.target.value});
		this.updatePosts();
	}

	changeCatgry = async (event) => {
		await this.setState({category: event.target.value});
		this.updatePosts();
	}


	search = async (event) => {
		await this.setState({search: event.target.value});
		this.updatePosts();
	}

	componentDidMount() {
		this.updatePosts()
		
	}
	

	updatePosts = () => {
		
		const {language, category, search} = this.state
		this.newsService.getAllNews(language, category, search)
		.then(this.onPostLoaded)
		.catch(this.onError)
		
	}
	
	onPostLoaded = (articles) => {
		
		this.setState({
			articles: articles,
			error: false,
			loading: false
		})
	}
	
	onError = (err) => {
		console.log(err)
		
		this.setState({
			error: true
		})
	}
	
	
	renderItems(arr) {
		
		
		return arr.map((articles, index) => {
			
			const {title, desc, link, image, imageSource} = articles;
			return(
				<div key={index} className="newsModule">
				<div className="newsModule__text">
				<h3 className="newsModule__text--title">{title}</h3>
				<p>{desc}</p>
				<a rel="noopener noreferrer" className="newsModule__text--link" href={link} target="_blank">Devamını Gör </a>
				</div>
				<img 
				src={image}
				className="newsModule__image" alt={imageSource}></img>
				</div>
				
				)

		});
	}

	loading() {
		let isDay = this.state.theme;
		if (isDay === 'day') {
			isDay = true
		}
		else if(isDay === 'night') {
			isDay = false
		}
		return(
			<Loading theme={isDay}/>
			)
	}



	render() {
		const {error, articles, loading} = this.state

		if (error) {
			console.log('hata')
		}

		let items;

		if (loading) {
			items = this.loading()
		}
		else {

			items = this.renderItems(articles);

		}
		
		return(
			<div className="news">
				<Theme ref={this.getTheme}/>
				<div className="newsForm">
					<h3 className="newsForm__title">Filtreler: </h3>
					<form className="newsForm__form">
						<div className="newsForm__select">

							<select className="select-css" id="selectKat" onChange={this.changeCatgry} value={this.state.category}>
								<option value="general">Hepsi</option>
								<option value="business">İş</option>
								<option value="entertainment">Eğlence</option>
								<option value="health">Sağlık</option>
								<option value="science">Bilim</option>
								<option value="sports">Spro</option>
								<option value="technology">Teknoloji</option>
							</select>
						</div>
						{
							/*<div className="newsForm__select">
						
							
							<select className="select-css" id="selectLang" onChange={this.changeLang} value={this.state.language}>
								<option value="ru">Russia</option>
								<option value="us">United States</option>
								<option value="tr">Türkiye</option>
							</select>
							
							</div>*/
						}
						<div className="newsForm__select">
							<input id="search" className="search-css" type="text" onChange={this.search} value={this.state.search} placeholder="Type a word.."/>
						</div>
					</form>

				</div>
				{items}

			</div>)
	}
}
